"use client";
import { useState } from 'react';
// import { fetchCsvData, DataRow } from '@/lib/csvFetcher';
// import SearchForm from '@/components/SearchForm';
// import ResultDisplay from '@/components/ResultDisplay';

export default function Home() {
  const [inputId, setInputId] = useState('');
  const [result, setResult] = useState<DataRow | null>(null);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setLoading(true);
    setError('');
    setResult(null);

    try {
      const data = await fetchCsvData();
      const found = data.find((row) => row.id === inputId);
      if (found) {
        setResult(found);
      } else {
        setError('ID not found');
      }
    } catch (err) {
      console.error(err);
      setError('An error occurred while fetching data.');
    }
    setLoading(false);
  };

  return (
    <div className="max-w-xl mx-auto p-6 bg-gray-50 rounded-lg shadow my-8">
      <h1 className="text-3xl text-center text-gray-800 mb-6">Check Your Hours</h1>
      {/* <SearchForm inputId={inputId} setInputId={setInputId} handleSubmit={handleSubmit} /> */}
      {loading && <p className="text-center text-gray-600">Loading...</p>}
      {error && <p className="text-center text-red-500">{error}</p>}
      {result && <ResultDisplay result={result} />}
    </div>
  );
}
